.Movie{
    background-color: white;
    width: 40%;
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    -ms-flex-align: start;
        align-items: flex-start;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-bottom: 50px;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    padding: 0 20px;
    -webkit-box-shadow: 0 8px 38px darkgray, 0 5px 12px gray;
            box-shadow: 0 8px 38px darkgray, 0 5px 12px gray;
}

.Movie__Column{
    width: 30%;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
}

.Movie__Column:last-child{
    padding: 20px 0;
    width: 60%;
}

.Movie h1{
    font-size: 20px;
    font-weight: 600;
}

.Movie .Movie__Genres{
    display: -ms-flexbox;
    display: flex;
    -ms-flex-wrap: wrap;
        flex-wrap: wrap;
    margin-bottom: 20px;
}

.Movie__Genres .Movie__Genres{
    margin-right: 10px;
    color: gray;
}

.Movie .Movie__Synopsis {
    -o-text-overflow: ellipsis;
       text-overflow: ellipsis;
    color: darkgray;
    overflow: hidden;
}

.Movie .Movie__Poster{
    max-width: 100%;
    position: relative;
    top: -20px;
    -webkit-box-shadow: -10px 19px 38px rgba(55, 52, 54, 0.3), 10px 15px 12px rgba(54, 54, 54, 0.2);
            box-shadow: -10px 19px 38px rgba(55, 52, 54, 0.3), 10px 15px 12px rgba(54, 54, 54, 0.2);
}

span.LinesEllipsis-ellipsis{
    width: 1.8rem;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    display: inline-block;
    text-align: center;
    border-radius: 0.5rem 0.5rem 0.5rem 0.5rem;
    background-color: lightgray;
    color: white;
    margin-left: 10px;
}

@media screen and (min-width:320px) and (max-width:667px){
    .Movie{
        width:100%;
    }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation : portrait) {
    .Movie{
        width: 100%;
        -ms-flex-direction: column;
            flex-direction: column;
    }
    .Movie__Poster{
        top : 0;
        left: 0;
        width: 100%;
    }
    .Movie__Column{
        width: 100% !important;
    }
}



